import React, { useState, useEffect } from 'react'

export default function Bin({ name, big, color, porcentage, state, rand }) {
    const [animation, setAnimation] = useState(state)
    useEffect(() => {
        setAnimation(state)
        setTimeout(() => { setAnimation('') }, 500)
    }, [rand])
    return (
        <div style={{ flex: 1, margin: '1vmin', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', height: '50vmin' }}>
            <div style={{ position: 'relative', animation: `${animation} 0.5s ease-in 0s` }}>
                <div style={{ position: 'absolute', height: `calc(100% - ${porcentage}%)`, width: '30vmin', overflow: 'hidden', top: 'auto', zIndex: 100 }}>
                    <img src={color} alt="bin" style={{ width: big ? '30vmin' : '20vmin', height: big ? '30vmin' : '20vmin', position: 'absolute', filter: 'brightness(0) invert(1)' }} />
                    <img src={color} alt="bin" style={{ width: big ? '30vmin' : '20vmin', height: big ? '30vmin' : '20vmin', position: 'absolute', opacity: 0.7 }} />
                </div>
                <img src={color} alt="bin" style={{ width: big ? '30vmin' : '20vmin', height: big ? '30vmin' : '20vmin', fill: 'red' }} />

            </div>
            <p>{name}</p>
        </div>
    )
}

