import React, { useEffect } from 'react'

export default function Trash({ trash, recicle }) {
    useEffect(() => { setTimeout(() => { recicle() }, 2000) }, [])
    return (
        <div style={{ height: '100vh', marginTop: '-60vmin', paddingLeft: '45vw' }}>
            <img src={trash} alt="bin" style={{ animation: 'fall 2s ease-in 0s', width: '15vmin', height: '15vmin', transform: `rotate(${Math.random() * 90 - 45}deg)` }} />
        </div>
    )
}
