import React from 'react'
import Bin from './Bin'
import LEFT from '../assets/png/left.png'
import RIGHT from '../assets/png/right.png'

export default function Bins({ bins, setBins, currentBin }) {
    return (
        <div style={{ display: 'flex', position: 'absolute', bottom: 0, width: '100vw', alignItems: 'center' }}>
            <img alt="left" style={{ width: '15vmin', height: '15vmin' }} src={LEFT} onClick={() => {
                let newBins = bins.slice(1, bins.length)
                setBins([...newBins, bins[0]])
            }} />
            <div style={{ flex: 1, display: 'flex', overflow: 'hidden', alignItems: 'center', justifyContent: 'center' }}>
                {bins.map(({ name, color, porcentage, state, rand }, index) => <Bin name={name} key={name} color={color} big={index === currentBin} porcentage={porcentage} state={state} rand={rand} />)}
            </div>
            <img alt="right" style={{ width: '15vmin', height: '15vmin' }} src={RIGHT} onClick={() => {
                let newBins = bins.slice(0, bins.length - 1)
                setBins([bins[bins.length - 1], ...newBins,])
            }} />
        </div>
    )
}
