import React, { useState, useRef, useEffect, useCallback } from 'react';
import Bins from './components/Bins'
import Trash from './components/Trash'
import { TRASH, BINS, EASY, STATES } from './constants'

function App() {
  const [level, setLevel] = useState(EASY)
  const [trash, setTrash] = useState()
  const [bins, setBins] = useState(BINS.filter((item) => level.find(type => type === item.name)));
  const [score, setScore] = useState(0)
  const currentBin = Math.floor(bins.length / 2)
  const binsRef = useRef(bins);
  binsRef.current = bins;
  const recicle = () => {
    compare()
    setTrash(getTrash());
  }
  const compare = () => {
    const newBins = [...binsRef.current]
    if (binsRef.current[currentBin].name === trash.type) {
      if (newBins[currentBin].porcentage === 100) {
        newBins[currentBin].porcentage = 0
        newBins[currentBin].state = STATES.full
        setScore(score + 1000)
      } else {
        newBins[currentBin].porcentage += 25
        newBins[currentBin].state = STATES.filling
      }
    } else {
      setScore(score - 100)
      newBins[currentBin].porcentage = 0
      newBins[currentBin].state = STATES.empty
    }
    newBins[currentBin].rand = Math.random()
    setBins(newBins)
  }
  const getTrash = useCallback(() => {
    const levelTrash = TRASH.filter((item) => level.find(type => type === item.type))
    return { ...levelTrash[Math.floor(Math.random() * levelTrash.length)], key: Math.random() + '' }
  }, [level])
  useEffect(() => {
    setTrash(getTrash());
  }, [getTrash])
  if (!trash) {
    return <div />
  }
  return (
    <div className="App">
      <p>{score}</p>
      <Trash trash={trash.name} key={trash.key} recicle={recicle} />
      <Bins bins={bins} setBins={setBins} currentBin={currentBin} />
    </div>
  );
}

export default App;
